export default {
    name: "app-model-ad",
    props: {
        opened: {
            type: Boolean,
            default() {
                return true;
            }
        },
        times: {
            type: Number,
            default() {
                return 1;
            }
        },
        list: {
            type: Array,
            default() {
                return [];
            }
        },
        multiple: {
            type: Boolean,
            default() {
                return false;
            }
        },
        mark: {
            type: String,
            default() {
                return 'fxhb';
            }
        },
        is_storage: Boolean,
    },
    data() {
        return {
            newList: [],
            popupActive: -1,
        };
    },
    computed: {
        listenChange() {
            return {
                multiple: this.multiple,
                list: this.list,
                is_storage: this.is_storage,
                times: this.times,
            };
        }
    },
    watch: {
        listenChange: {
            handler(v) {
                this.init()
            },
            immediate: true
        }
    },
    methods: {
        init() {
            if (!this.multiple) {
                this.newList = [this.list];
            } else {
                this.newList = this.list;
            }
            if (!this.opened) {
                return;
            }
            if (typeof this.$popupAd.list[this.mark] === 'undefined') {
                this.$popupAd.list[this.mark] = true;
                this.$event.on(this.$const.EVENT_POPUP, false).then(mark => {
                    if (mark === this.mark) {
                        this.showPopupModal();
                    }
                });
            }
            this.showPopupModal();
        },
        showPopupModal() {
            if (this.$popupAd.show) {
                return;
            }
            let first = 'popUpFirst' + this.mark;
            this.$popupAd.show = this.mark;
            let active = -1;
            if (this.times === 0) {
                active = 0;
            } else {
                if (this.$popupAd.first[first]) {
                    active = -1;
                } else {
                    active = 0;
                }
            }
            if (!this.is_storage) {
                this.$popupAd.first[first] = true;
            }
            this.popupActive = active;
            this.nextModal();
        },
        nextModal() {
            if (this.list.length <= this.popupActive || this.popupActive === -1) {
                let list = this.$popupAd.list;
                let next = false;
                for (let i in list) {
                    if (next) {
                        next = false;
                        this.$event.trigger(this.$const.EVENT_POPUP, i);
                        break;
                    }
                    if (i === this.mark) {
                        next = true;
                    }
                }
                if (!next) {
                    this.$popupAd.show = null;
                }
            }
        },
        closePopupModal() {
            let popupActive = this.popupActive;
            popupActive += 1;
            this.popupActive = popupActive;
            this.$popupAd.show = null;
            this.nextModal();
        }
    }
}
